/* ----- FONDO ARCADE ----- */
body {
  margin: 0;
  padding: 25px;
  text-align: center;
  background: radial-gradient(circle at center, #000020, #000010, #000000);
  font-family: "Press Start 2P", monospace; /* FUENTE PIXEL ARCADE */
  color: #ffe500;
}

/* ----- TÍTULO ----- */
h1 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #ffdd33;
  text-shadow: 
      0 0 8px #ffeb3b,
      0 0 18px #ffeb3b,
      0 0 30px #ffea00;
}

/* ----- CONTENEDOR PRINCIPAL ----- */
.game {
  display: inline-block;
  padding: 20px;
  border-radius: 12px;
  background: #001133;
  box-shadow: 
      0 0 15px #0033aa,
      0 0 30px #0055ff inset,
      0 0 25px #000000;
  border: 4px solid #0044cc;
}

/* ----- BOTONES ----- */
button {
  font-family: "Press Start 2P", monospace;
  font-size: 12px;
  cursor: pointer;
  padding: 10px 16px;
  margin: 6px;
  border: none;
  border-radius: 6px;
  background: #ff0044;
  color: #fff;
  box-shadow: 
      0 0 10px #ff0044,
      0 0 20px #ff3366 inset;
  transition: 0.15s ease;
}

button:hover {
  background: #ff5577;
  transform: scale(1.08);
}

/* RESET */
.reset-btn {
  background: #ffbb00;
  color: #000;
  box-shadow: 0 0 10px #ffbb00;
}
.reset-btn:hover {
  background: #ffdd55;
}

/* ----- SELECT ----- */
select {
  font-family: "Press Start 2P";
  padding: 8px;
  background: #000820;
  color: #ffeb3b;
  border: 2px solid #ffcb00;
  border-radius: 4px;
  box-shadow: 0 0 10px #ffeb3b;
}

/* ----- TABLERO ----- */
.game-board {
  margin: 20px auto;
  padding: 15px;
  background: #000015;
  border: 4px solid #0033aa;
  border-radius: 8px;
  display: inline-block;
  box-shadow: 
      0 0 8px #0044ff inset,
      0 0 25px #001133;
}

/* ----- FILAS DEL TABLERO ----- */
.board-row {
  display: flex;
}

/* ----- CASILLAS ----- */
.square {
  width: 75px;
  height: 75px;
  background: #000;
  border: 3px solid #0033aa;
  margin: 3px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;

  transition: 0.15s ease;

  /* Glow estilo LED */
  box-shadow: 
      0 0 10px #002288,
      0 0 20px #0033aa inset;
}

.square:hover {
  background: #000820;
  transform: scale(1.05);
}

/* IMÁGENES PIEZAS */
.piece {
  width: 70%;
  height: 70%;
  object-fit: contain;
}

/* ----- TEXTO ESTADO ----- */
.status {
  font-size: 14px;
  margin-bottom: 10px;
  color: #00eaff;
  text-shadow: 
      0 0 6px #00eaff,
      0 0 15px #009dcc;
}

/* ----- HISTORIAL DE JUGADAS ----- */
.game-info {
  margin-top: 20px;
}

.game-info button {
  background: #003cff;
  box-shadow: 0 0 10px #0066ff;
}

.game-info button:hover {
  background: #4d79ff;
}

/* ----- MENSAJE DE GANADOR ----- */
.winner-message {
  margin-top: 20px;
  font-size: 18px;
  color: #00ff88;
  text-shadow: 
      0 0 12px #00ff88,
      0 0 25px #00cc66;
  animation: popup 0.6s ease-out;
}

@keyframes popup {
  0% { transform: scale(0.5); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

.winner-message {
  margin-top: 20px;
  font-size: 24px;
  font-family: "Press Start 2P", monospace;
  color: #00ff88;
  text-shadow:
      0 0 10px #00ff88,
      0 0 20px #00cc66,
      0 0 35px #009944;
  animation: winnerPop 0.6s ease-out;
}

/* ----- MENSAJE DE EMPATE ----- */
.draw-message {
  margin-top: 20px;
  font-size: 22px;
  font-family: "Press Start 2P", monospace;
  color: #4ddbff;
  text-shadow:
      0 0 10px #4ddbff,
      0 0 20px #33bbff,
      0 0 30px #1a8cff;
  animation: drawPop 0.6s ease-out;
}

@keyframes drawPop {
  0% { transform: scale(0.4); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}


@keyframes winnerPop {
  0% { transform: scale(0.4); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

